@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


html,
body {
  min-width: 290px;
  background-color: #dbe9d8;  
  line-height: 1.5;
  font-family: 'Josefin Sans', sans-serif;
  font-size: large;


}

h1 {
  font-size: 55px;
}

h2 {
  font-size: 35px;;
}

.text-center {
  color: #8C889B;
  font-weight: bold;
  margin: 10px;
  text-align: center;
  font-weight: thin;
}

.preview {
  background-color:#f2efe8;
  border: 15px solid #c2d4d8;
border-radius: 3px;
}

.container {
  text-align: center;
}

textarea {
  background-color:#f2efe8;
  min-height: 400px;
  max-width: 100%;
  min-width: 100%;
border-radius: 3px;
border: 15px solid #c2d4d8;
}

.preview {
  min-height: 200px;
  max-width: 75%;
  display: inline-block;
}
.col-6 {
  width: 75%;
  display: inline-block;
}

.row-after {
  content: "";
  display: table;
  clear: both;
}





